import React from 'react';
import classNames from 'classnames';

const FooterNav = ({
  className,
  ...props
}) => {

  const classes = classNames(
    'footer-nav',
    className
  );

  return (
    <nav
      {...props}
      className={classes}
    >

              <div className="footer-copyright">Thanks for listening</div>
    </nav>
  );
}

export default FooterNav;